export default {
	name: 'UniTag',
	props: {
		type: { // 标签类型default、primary、success、warning、danger、royal
			type: String,
			default: 'default'
		},
		size: { // 标签大小 normal, small
			type: String,
			default: 'normal'
		},
		text: {
			type: String,
			default: ''
		}, // 标签内容
		disabled: { // 是否为禁用状态
			type: Boolean,
			default: false
		},
		inverted: { // 是否为空心
			type: Boolean,
			default: false
		},
		circle: { // 是否为圆角样式
			type: Boolean,
			default: false
		},
		mark: { // 是否为标记样式
			type: Boolean,
			default: false
		}
	},
	methods: {
		_onClick() {
			if (this.disabled) {
				return
			}
			this.$emit('click')
		}
	}
}
